  _divisorLine(geometry) {
    selfCm.idxLines = selfCm.idxLines + 1
    const nameIdLine = `Polyline_${selfCm.idxLines}`

    const polylineGeomUtm = webMercatorUtils.webMercatorToGeographic(geometry)
    let vertices = polylineGeomUtm.paths[0];
    const lastTwoCoords = vertices.slice(-2);
    const line = turf.lineString(lastTwoCoords);
    const bearing = turf.bearing(turf.point(line.geometry.coordinates[0]), turf.point(line.geometry.coordinates[1]));
    const options = { units: 'meters' };
    const addDistance = 0.1
    const point = turf.point(line.geometry.coordinates[1]);
    const destintation = turf.destination(point, addDistance, bearing, options);

    // reverse coordinates of line
    let vertices2 = [...vertices];
    vertices2.reverse();
    const lastTwoCoords2 = vertices2.slice(-2);
    const line2 = turf.lineString(lastTwoCoords2);
    const bearing2 = turf.bearing(turf.point(line2.geometry.coordinates[0]), turf.point(line2.geometry.coordinates[1]));
    const point2 = turf.point(line2.geometry.coordinates[1]);
    const destintation2 = turf.destination(point2, addDistance, bearing2, options);

    vertices[vertices.length - 1] = destintation.geometry.coordinates;
    // add vertices2 first element to vertices
    vertices.unshift(destintation2.geometry.coordinates);

    const polyline = new Polyline({
      paths: [vertices],
      spatialReference: polylineGeomUtm.spatialReference
    });
    let response = webMercatorUtils.geographicToWebMercator(polyline);

    const graphic = new Graphic(response, symbolDivisionLote, { id: nameIdLine });
    graphicLayerLineaDivision.add(graphic);
    selfCm._populateTableDrawLine(nameIdLine)
    selfCm._addNameToLine(nameIdLine, response)
    selfCm.map.addLayer(graphicLayerLineaDivision);
    selfCm.map.setInfoWindowOnClick(true);
    toolbarCm.deactivate();
    selfCm._removeClassActiveButton()
  },

