  // crear una funcion que permita dos poligonos aceptando dos poligonos json como parametros
  // la funcion debe retornar un poligono json
  _unionFeatures(arr) {
    const union = geometryEngine.union(arr);
    return union;
  },

  _unionFeaturesAcumulation(topology = false) {
    // Creamos grafico de lote fusionado
    // const graphicLayerLotPreview = new GraphicsLayer({
    //   id: idGraphicLoteCm
    // });

    const arr = selfCm.currentLotsRows.map((i) => i.geometry);

    const response = selfCm._unionFeatures(arr);
    if (topology) {
      if (response.rings.length > 1) {
        const deletedRings = []
        for (let a of response.rings) {
          const polygonA = new Polygon({
            rings: [a],
            spatialReference: response.spatialReference
          });
          for (let b of response.rings) {
            if (a !== b) {
              const polygonB = new Polygon({
                rings: [b],
                spatialReference: response.spatialReference
              });
              const contain = geometryEngine.within(polygonA, polygonB);
              if (contain) {
                deletedRings.push(b);
              }
            }
          }
        };
        const polygonParents = [];
        for (let ring of response.rings) {
          if (deletedRings.includes(ring)) {
            continue;
          }
          const polygon = new Polygon({
            rings: [ring],
            spatialReference: response.spatialReference
          });
          polygonParents.push(polygon);
        };

        const check = geometryEngine.union(polygonParents);

        if (check.rings.length > 1) {
          throw new Error("La acumulacin no es posible (los predios no son contiguos)")
        }
      }
    }


    const graphic = new Graphic(response, symbolFusionLote, { tipLot: 2 });

    graphicLayerLotPreview.add(graphic);
    // selfCm.map.addLayer(graphicLayerLotPreview);
    // selfCm.map.setExtent(graphic._extent.expand(1.5), true);
    // return [response];
  },

